import React from 'react'
import styles from './Movie.module.css'
import { ReactComponent as Watch } from '../../assets/svg/watch.svg'

export default function Movie(props) {
  return (
    <div className={styles.root}>
      {
        props.title && <div className={styles.title}>{props.title}</div>
      }
      <div className={`${styles.videoWrapper} ${props.middle ? styles.videoWrapperMiddle : ''}`}>
        {
          props.showMovie && <video
            className={styles.video}
            src={props.src}
            controls={'controls'}
          />
        }
      </div>
      {
        props.youtubeLink && <div className={styles.gotoLinkWrapper}>
          <a className={styles.gotoLink} rel="noopener noreferrer" href={props.youtubeLink} target="_blank">
            <Watch/>
          </a>
        </div>
      }
    </div>
  )
}