import React from 'react'
import { useHistory } from 'react-router-dom'
import styles from './Main.module.css'

import { ReactComponent as Rhombus } from '../../assets/svg/rhombus.svg'
import { ReactComponent as Logo } from '../../assets/svg/logo.svg'
import { ReactComponent as Play } from '../../assets/svg/play.svg'


import manPicture from '../../assets/images/man.png'

export default function Main() {
  const history = useHistory()

  // Handlers
  const enterHandler = () => {
    history.push('/hotel')
  }

  return (
    <div className={styles.root}>
      <div className={styles.header}>
        <div className={styles.logo}>
          <Logo/>
        </div>
      </div>
      <div className={styles.content}>
        <div className={styles.play}>
          <div className={styles.playIcon}>
            <Play/>
          </div>
        </div>
        <div className={styles.enter} onClick={enterHandler}>
          Зайти в&nbsp;гости
        </div>
        <div className={styles.rhombusBox}>
          <div className={styles.rhombusBoxIcon}>
            <Rhombus/>
          </div>
        </div>
        <div className={styles.gotoRG}>
          <a className={styles.gotoRGLink} rel="noopener noreferrer" href="//rg.ru" target="_blank">
            Перейти на&nbsp;RG.RU
          </a>
        </div>
      </div>

      <img className={styles.manPicture} src={manPicture} alt=''/>
      <div className={styles.footer}/>
    </div>
  )
}