import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from 'react-router-dom';

import Hotel from './components/Hotel';
import Main from './components/Main';

function App() {
  return (
    <Router>
      <div className='app'>
        <Switch>
          <Route path='/hotel'>
            <Hotel />
          </Route>
          <Route path='/'>
            <Main />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
